	module.exports = {"data":{"markdownRemark":{"html":"<h1>Highway to hell</h1>\n<p><img src=\"/emasdeu/PICO-8_0-56e4f886c03d0c82169107a9ed6aab1a.gif\" alt=\"Gif 1\"></p>\n<p><img src=\"/emasdeu/PICO-8_0-56e4f886c03d0c82169107a9ed6aab1a.gif\"\n     alt=\"Markdown Monster icon\"\n     style=\"float: right; margin-left: 10px;\" /></p>\n<pre><code class=\"language-lua\">function _init()\n i=0\n color=8\n t=time()\n cls()\nend\n\nfunction _update60()\n  x=64+(32+sin(t)*7*sin(8*i+t/2))*cos(i)\n  y=64+(32+sin(t)*7*sin(8*i+t/2))*sin(i)\n\n  i+=0.01\n  if i>1 then\n    i=0\n    t=time()\n    color+=1\n  end\nend\n\nfunction _draw()\n  circfill(x,y,2,color)\n  flip()\nend\n</code></pre>","frontmatter":{"path":"/post1","date":"2017-07-12T17:12:33.962Z","title":"My First Gatsby Post"}}},"pathContext":{}}

